/* Slider */

.slick-slider
  position: relative
  display: block
  box-sizing: border-box
  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none
  -webkit-touch-callout: none
  -khtml-user-select: none
  -ms-touch-action: pan-y
  touch-action: pan-y
  -webkit-tap-highlight-color: transparent

.slick-list
  position: relative
  display: block
  overflow: hidden
  margin: 0 -20px
  padding: 0

  &:focus
    outline: none

  &.dragging
    cursor: pointer
    cursor: hand

.slick-slider
  .slick-track, .slick-list
    -webkit-transform: translate3d(0, 0, 0)
    -moz-transform: translate3d(0, 0, 0)
    -ms-transform: translate3d(0, 0, 0)
    -o-transform: translate3d(0, 0, 0)
    transform: translate3d(0, 0, 0)

.slick-track
  position: relative
  top: 0
  left: 0
  display: block
  margin-left: auto
  margin-right: auto

  &:before
    display: table
    content: ''

  &:after
    display: table
    content: ''
    clear: both

.slick-loading .slick-track
  visibility: hidden

.slick-slide
  margin: 0px 20px
  display: none
  float: left
  height: 100%
  min-height: 1px

[dir='rtl'] .slick-slide
  float: right

.slick-slide
  img
    display: block

  &.slick-loading img
    display: none

  &.dragging img
    pointer-events: none

.slick-initialized .slick-slide
  display: block

.slick-loading .slick-slide
  visibility: hidden

.slick-vertical .slick-slide
  display: block
  height: auto
  border: 1px solid transparent

.slick-arrow.slick-hidden
  display: none

ul.slick-dots
  text-align: center
  z-index: 1
  display: flex
  justify-content: center
  position: absolute
  left: 50%
  transform: translate(-50%, 0%)
  padding-left: 0
  bottom: 0
  @media (min-width: 900px)
    bottom: 30px
    gap: 10px
  li
    display: flex
    align-items: center
    &::after
      display: none
    button
      overflow: hidden
      margin: 0 8px
      padding: 0
      background: $primary-color
      border: none
      color: $primary-color
      cursor: pointer
      width: 10px
      height: 10px
      border-radius: 50%
      outline: none
      @media screen and (min-width: 900px)
        width: 70px
        height: 3px
        border-radius: 0
    &.slick-active
      button
        color: $colorRed
        background: $colorRed
        height: 10px
        @media screen and (min-width: 900px)
          height: 3px

.content_slider
  ul.slick-dots
    margin-top: -85px
    text-align: center
    z-index: 150
    display: flex
    justify-content: center
    position: absolute
    bottom: -30px
    left: 50%
    transform: translate(-50%, 0%)
    li
      display: flex
      align-items: center
      &::after
        display: none
      button
        overflow: hidden
        width: 20px
        height: 20px
        margin: 0 8px
        padding: 0
        background: #BEBEBE
        border: none
        color: #BEBEBE
        cursor: pointer
        border-radius: 30px
      &.slick-active
        button
          color: #022A58
          background: #022A58
          height: 20px

.image-slider
  .slick-arrow
    &.slick-prev, &.slick-next
      outline: none
      cursor: pointer
      transform: translateY(-50%)
      content: ''
      width: 0
      height: 0
      color: transparent
      background: transparent
      border: none
      position: absolute
      right: 0
      top: 0
    &.slick-prev
      z-index: 1
      top: calc(50% - 36.5px)
      left: -4px
      &:after
        content: ""
        display: inline-block
        background-color: $colorRed
        background-position: center
        background-repeat: no-repeat
        background-size: 18.55px 18.55px
        width: 53px
        height: 53px
        border-radius: 0 0 10px 0
    &.slick-next
      z-index: 1
      top: calc(50% - (-44.5px) )
      right: -4px
      rotate: 180deg
    &:after
      content: ""
      display: inline-block
      background-color: $colorRed
      background-position: center
      background-repeat: no-repeat
      background-size: 18.55px 18.55px
      width: 53px
      height: 53px
      border-top-right-radius: 10px
